<script>
import { ref } from "vue";
import { useRouter } from "vue-router";
import LoaderBar from "../components/LoaderBar.vue";

export default {
  name: "Preventivos",
  components: { LoaderBar },

  setup(props) {
    const router = useRouter();
    let porcentaje = ref(0);

    var fecha = new Date();
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaFin = ref(ano + "-" + mes + "-" + dia);

    fecha.setDate(fecha.getDate() - 7);
    var mes = fecha.getMonth() + 1;
    var dia = fecha.getDate();
    var ano = fecha.getFullYear();
    if (dia < 10) dia = "0" + dia;
    if (mes < 10) mes = "0" + mes;
    const fechaInicio = ref(ano + "-" + mes + "-" + dia);

    // ARRAYS
    const arrayData = ref([]);
    const arraySecciones = ref([]);
    const arrayRanking = ref([]);
    const arrayTabla = ref([]);
    const idDestino = ref(localStorage.getItem("idDestino"));
    const idUsuario = ref(localStorage.getItem("usuario"));

    const viewAmerica = () => {
      if (localStorage.getItem("idDestino") == 10) {
        router.push("/America");
        return;
      }
    };
    viewAmerica();

    const ranking = async () => {
      console.log(fechaInicio.value, fechaFin.value);

      try {
        const resp = await fetch(
          `../apis/reportes.php?action=rankingPreventivos&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}`
        );
        arrayRanking.value = await resp.json();
      } catch (error) {
        console.log(error);
      }
    };
    ranking();

    const fetchDatos = async () => {
      try {
        const res = await fetch(
          `../apis/reportes.php?action=preventivos&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}`
        );
        arrayData.value = await res.json();
      } catch (error) {
        console.log(error);
      }
    };
    fetchDatos();

    const fetchTabla = async () => {
      try {
        let myTimer = setInterval(() => {
          porcentaje.value += 7;
          if (porcentaje.value >= 99) {
            porcentaje.value = 0;
          }
        }, 1000);

        const res = await fetch(
          `../apis/reportes.php?action=reportePreventivosEquiposSeccion&idDestino=${idDestino.value}&idUsuario=${idUsuario.value}&fechaInicio=${fechaInicio.value}&fechaFin=${fechaFin.value}`
        );
        arrayTabla.value = await res.json();
        clearInterval(myTimer);
        porcentaje.value = 100;
      } catch (error) {
        porcentaje.value = 100;
        console.log(error);
      }
    };
    fetchTabla();

    return {
      arrayData,
      arraySecciones,
      arrayRanking,
      fechaInicio,
      fechaFin,
      ranking,
      fetchDatos,
      idDestino,
      arrayTabla,
      porcentaje,
    };
  },
};
</script>

